body {
  margin: 0;
}

.container {
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch__hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}

@keyframes rotate {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  100% {
    transform: translateX(-50%)rotate(359deg);
  }
}
